.rtr-root {
    position: relative;
    display: inline-block;
    overflow: hidden;
}

.rtr-ripple-wrapper {
    display: block;
    opacity: 1;
    pointer-events: none;
}

.rtr-ripple {
    position: absolute;
    width: 50px;
    height: 50px;
    left: 0;
    top: 0;
    border-radius: 50%;
    opacity: 0;
}

.rtr-ripple-wrapper-exiting {
    opacity: 0;
    -webkit-animation: rtr-ripple-exit 500ms cubic-bezier(0.4, 0, 0.2, 1);
            animation: rtr-ripple-exit 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.rtr-ripple-entering {
    opacity: 0.3;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    -webkit-animation: rtr-ripple-enter 500ms cubic-bezier(0.4, 0, 0.2, 1);
            animation: rtr-ripple-enter 500ms cubic-bezier(0.4, 0, 0.2, 1)
}

@-webkit-keyframes rtr-ripple-enter {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes rtr-ripple-enter {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@-webkit-keyframes rtr-ripple-exit {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes rtr-ripple-exit {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}


